// Common
import React, { useState } from 'react';
import cx from 'classnames';

// Components
import ImageUploader from "./ImageUpload";
import TagList from '../../TagList';

// Styles
import styles from './styles.module.scss';

const StepOne = ({ tagsData }) => {
  const [images, setImages] = useState([]);
  const [selectedTags, onSelectTag] = useState([]);

  const onDrop = img => {
    setImages(img);
  };

  console.log('images', images)
  console.log('selectedTags', selectedTags)

  return (
    <div className={cx(styles['form'])}>
      <div className={styles['form_body']}>
        <div className={styles['form_left-block']}>
          <div className={cx(styles['form_field'], styles['form_field-user'])}>
            <input
              type="text"
              className={styles['form_field-input']}
              placeholder="Ваше ім'я"
            />
            <input
              type="email"
              className={styles['form_field-input']}
              placeholder="Ваш E-mail"
            />
          </div>
          <div className={cx(styles['form_field'], styles['form_field-attachments'])}>
            <h4 className={styles['form_subtitle']}>Додайте фото зафіксованого порушення <sup>*</sup></h4>
            <div className={styles['form_field-attachments_body']}>
              <ImageUploader
                onChange={onDrop}
                images={images}
                setImages={setImages}
              />
            </div>
          </div>
        </div>
        <div className={styles['form_right-block']}>
          <div className={cx(styles['form_field'], styles['form_field-tags'])}>
            <h4 className={styles['form_subtitle']}>Виберіть категорії, котрі описують порушення <sup>*</sup></h4>
            {
              tagsData && (
                <TagList
                  className={styles['form_field-tagList']}
                  tagList={tagsData}
                  onSelect={onSelectTag}
                  selectedItems={selectedTags}
                  isSelectList
                />
              )}
          </div>
        </div>
      </div>
    </div>
  )
}

export default StepOne;
