// Common
import React from 'react';
import cx from 'classnames';

// Styles
import styles from './styles.module.scss';

const isSelected = (list, uuid) => {
  if (!list || !list.length || uuid) return false;
  return list.some(item => item.uuid === uuid)
};

const TagList = ({
  tagList,
  onSelect,
  className,
  isSelectList,
  selectedItems
}) => {
  if (!tagList || !tagList.length) return null;

  return (
    <ul className={cx(
      {
        [className]: className,
        [styles['tag-list']]: true,
        [styles['tag-list_select']]: isSelectList
      },
    )}>
      {tagList.map(({ name, uuid }) =>
        <li
          key={uuid}
          className={cx(
            styles['tag-list_item'], {
              [styles['tag-list_item-selected']]: isSelected(selectedItems, uuid)
            }
          )}
          onSelect={onSelect}
        >
          {name}
        </li>
      )}
    </ul>
  )
}

export default TagList;
